<template>
  <div class="container">
    <div class="loginbox">
      <!--ref是对model的引用对象 -->
      <a-form-model
        ref="loginformret"
        class="loginform"
        :model="formdata"
        :rules="rules"
      >
        <a-form-model-item label="account" prop="username">
          <a-input placeholder="请输入用户名" v-model="formdata.username">
            <!-- 这里是对view对值 下面是对model对值-->
            <a-icon
              slot="prefix"
              type="user"
              style="color: rgba(0, 0, 0, 0.25)"
            />
          </a-input>
        </a-form-model-item>

        <a-form-model-item label="password" prop="password">
          <a-input
            placeholder="请输入密码"
            type="password"
            v-model="formdata.password"
            v-on:keyup.enter="login"
          >
          <!--这里在input元素里面添加v-on监听事件 根据键盘按enter调用login方法-->
          <!--给元素添加事件监听  相当于每一个元素可以通过键盘和鼠标的点击操控然后调用我们设置好的方法 -->
            <a-icon
              slot="prefix"
              type="lock"
              style="color: rgba(0, 0, 0, 0.25)"
            />" />
          </a-input>
        </a-form-model-item>

        <a-form-model-item class="loginbtn">
          <a-button type="primary" style="margin: 0px" @click="login">登陆</a-button>
          <a-button type="info" style="margin: 10px" @click="reset">取消</a-button>
          <!-- @click 为前端按钮对象添加一个方法-->
        </a-form-model-item>
      </a-form-model>
    </div>
  </div>
</template>
<!-- scoped 只对当前这个页面有效-->

<script>
// 导出组件给外部使用 组件是多个功能模块的集合 上面是对象
// 先写外部需要用到的值 再写内部逻辑
export default {
  data () {
    return {
      formdata: {
        username: '',
        password: ''
      },
      // rules 只是对表单里面对参数进行规格化
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          {
            min: 4,
            max: 12,
            message: '用户名必须在4到12个字符之间',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 20,
            message: '密码必须在6到20个字符之间',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    reset () {
      // 在函数里 this指的是全局对象 window对象在浏览器中有两重身份，一个是ECMAScript中的Global对象  ¥refs是对上main的引用对象
      this.$refs.loginformret.resetFields()
    },
    login () {
      // 1.调用calidate验证方法
      this.$refs.loginformret.validate(async (valid) => { // 2.如果无效会返回错误提示
        if (!valid) return this.$message.error('输入数据有误，请重新输入')
        // 3.这里使用的是对象的解构赋值 将等式右边等值赋值给等式左边的res
        const { data: res } = await this.$http.post('http://121.196.163.8:8081/api/v1/login', this.formdata)
        console.log(res)
        // 4.如果填写的数据有效 这里调用全局对象再调用axios的post方法将表单的数据传入后台 第一个参数是url与后台绑定的标识
        // 5.这里请求成功的话 就返回一个回复 如果状态码不是200 我们还是提示错误信息
        if (res.status !== 200) return this.$message.error(res.message)
        window.sessionStorage.setItem('token', res.token)
        // 如果状态码是200 我们就将后台发放的token保存在浏览器里面 下次就不用登陆了  sessionstorage如果浏览器关闭后之后登陆要登陆 localstorage不会 它会把token一直保存在本地
        // 我们把后端传过来的token设置到sessionstorage里面后 就跳转到主页
        this.$router.push('admin/index')
        // 在 Vue 实例内部，你可以通过 $router 访问路由实例。因此你可以调用 this.$router.push
      })
    }
  }
}
</script>
<!--登陆界面功能 完成对表单数据对验证并传入后台-->
