<template>
  <div id="header-div">
    <a-button type="primary" @click="toggleCollapsed">
      <a-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'" />
    </a-button>

    <a-button type="danger" @click="loginOut"> 退出</a-button>
  </div>
</template>

<script>
export default {
  props: ['collapsed'],
  methods: {
    // 退出功能实现: 将token清除 然后跳转到登陆界面
    loginOut () {
      window.sessionStorage.clear('token')
      this.$router.push('/login')
    },
    toggleCollapsed () {
      this.$emit('isClose')
    }
  }
}
</script>

<style scoped>
#header-div {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
}
</style>
