import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Admin from '../views/Admin.vue'

// 页面路由组件 admin下面的组件
import Index from '../components/admin/Index.vue'
import AddArt from '../components/article/AddArt.vue'
import ArtList from '../components/article/ArtList.vue'
import CateList from '../components/category/CateList.vue'
import UserList from '../components/user/UserList.vue'

Vue.use(VueRouter)

// 路由常量 路由组
const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    children: [
      // 这里还是跳转到一个新页面
      { path: 'index', component: Index },
      { path: 'addart', component: AddArt },
      { path: 'addart/:id', component: AddArt, props: true },

      { path: 'artlist', component: ArtList },
      { path: 'catelist', component: CateList },
      { path: 'userlist', component: UserList }
    ]
  }
]

// 创建一个路由组
const router = new VueRouter({
  routes
})

// 路由守卫
router.beforeEach((to, from, next) => {
  // 1.拿到token
  const token = window.sessionStorage.getItem('token')
  // 2.如果第一次来到login页面就放行
  if (to.path === '/login') return next()
  // 3.如果是从login页面进行登陆且有权限就跳转到admin页面
  if (to.path === '/admin' && !token) { return next('/login') } else { return next() }
})

// 导入router路由模块 为了能给其他模块使用
export default router
