<template>
  <div>
    <a-card>
      <h3>{{ id ? "编辑文章" : "新增文章" }}</h3>

      <a-form-model
        :model="artInfo"
        ref="artInfoRef"
        :rules="artInfoRules"
        :hideRequiredMark="true"
      >
        <a-row :gutter="24">
          <a-col :span="16">
            <a-form-model-item label="文章标题" prop="title">
              <a-input style="width: 300px" v-model="artInfo.title"></a-input>
            </a-form-model-item>
            <a-form-model-item label="文章描述" prop="desc">
              <a-input type="textarea" v-model="artInfo.desc"></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="8">
            <a-form-model-item label="文章分类" prop="cid">
              <a-select
                style="width: 200px"
                v-model="artInfo.cid"
                placeholder="请选择分类"
                @change="cateChange"
              >
                <a-select-option
                  v-for="item in Catelist"
                  :key="item.id"
                  :value="item.id"
                >
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>

            <a-form-model-item label="文章缩略图" prop="img">
              <a-upload
                listType="picture"
                name="file"
                :action="upUrl"
                :headers="headers"
                @change="upChange"
              >
                <a-button> <a-icon type="upload" />点击上传 </a-button>

                <template v-if="id">
                  <img
                    :src="artInfo.img"
                    style="width: 120px; height: 100px; margin-left: 15px"
                  />
                </template>
              </a-upload>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-form-model-item label="文章内容" prop="content">
          <div id="main">
    <mavon-editor v-model="artInfo.content"/>
</div>
        </a-form-model-item>

        <a-form-model-item>
          <a-button
            type="danger"
            style="margin-right: 15px"
            @click="artOk(artInfo.id)"
          >
            {{ artInfo.id ? "更新" : "提交" }}
          </a-button>
          <a-button type="primary" @click="addCancel">取消</a-button>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  props: ['id'],
  data () {
    return {
      value: '',
      artInfo: {
        id: 0,
        title: '',
        cid: undefined,
        desc: '',
        content: '',
        img: ''
      },
      // 这里对应后端上传接口
      upUrl: 'http://121.196.163.8:8081/api/v1/upload',
      Catelist: [],
      headers: {},
      artInfoRules: {
        title: [
          { required: true, message: '请输入文章标题', trigger: 'change' }
        ],
        cid: [{ required: true, message: '请选择文章分类', trigger: 'change' }],
        desc: [
          { required: true, message: '请输入文章描述', trigger: 'change' },
          { max: 120, message: '描述最多可写120个字符', trigger: 'change' }
        ],
        img: [
          { required: true, message: '请选择文章缩略图', trigger: 'change' }
        ],
        content: [
          { required: true, message: '请输入文章内容', trigger: 'change' }
        ]
      }
    }
  },
  mounted () {
    this.getCateList()
    this.headers = {
      Authorization: `Bearer ${window.sessionStorage.getItem('token')}`
    }
    if (this.id) {
      this.getArtInfo(this.id)
    }
  },
  methods: {
    // 查询文章信息
    async getArtInfo (id) {
      // 这里对应查询后台单个文章信息接口
      const { data: res } = await this.$http.get(
        `http://121.196.163.8:8081/api/v1/article/info/${id}`
      )
      if (res.status !== 200) {
        if (res.status === 1004) {
          window.sessionStorage.clear()
          this.$router.push('/login')
        }
        this.$message.error(res.message)
      }
      this.artInfo = res.data
      this.artInfo.id = res.data.ID
    },
    // 获取分类列表
    async getCateList () {
      // 这里对象后端请求分类列表接口
      const { data: res } = await this.$http.get(
        'http://121.196.163.8:8081/api/v1/category'
      )
      if (res.status !== 200) {
        if (res.status === 1004) {
          window.sessionStorage.clear()
          this.$router.push('/login')
        }
        this.$message.error(res.message)
      }
      this.Catelist = res.data
    },
    // 选择分类
    cateChange (value) {
      this.artInfo.cid = value
    },
    // 上传图片
    upChange (info) {
      if (info.file.status !== 'uploading') {
      }
      if (info.file.status === 'done') {
        this.$message.success('图片上传成功')
        this.artInfo.img = info.file.response.url
      } else if (info.file.status === 'error') {
        this.$message.error('图片上传失败')
      }
    },
    // 提交&&更新文章
    artOk (id) {
      this.$refs.artInfoRef.validate(async (valid) => {
        if (!valid) { return this.$message.error('参数验证未通过，请按要求录入文章内容') }
        if (id === 0) {
          // 这里对应后台添加文章
          const { data: res } = await this.$http.post(
            'http://121.196.163.8:8081/api/v1/article/add',
            this.artInfo
          )
          if (res.status !== 200) return this.$message.error(res.message)
          this.$router.push('/admin/artlist')
          this.$message.success('添加文章成功')
        } else {
          // 这里对应后台根据文章id编辑文章
          const { data: res } = await this.$http.put(
            `http://121.196.163.8:8081/api/v1/article/${id}`,
            this.artInfo
          )
          if (res.status !== 200) return this.$message.error(res.message)

          this.$router.push('/admin/artlist')
          this.$message.success('更新文章成功')
        }
      })
    },

    addCancel () {
      this.$refs.artInfoRef.resetFields()
    }
  }
}
</script>
