import Vue from 'vue'
import { Button, FormModel, Input, Icon, message, Layout, Menu, Card, Table, Row, Col, Modal, Select, Upload } from 'ant-design-vue'
Vue.prototype.$message = message
Vue.prototype.$confirm = Modal.confirm

message.config({
  top: '100px',
  duration: 2,
  maxCount: 3
})
Vue.use(Button)
Vue.use(FormModel)
Vue.use(Input)
Vue.use(Icon)
Vue.use(Layout)
Vue.use(Menu)
Vue.use(Card)
Vue.use(Table)
Vue.use(Row)
Vue.use(Col)
Vue.use(Modal)
Vue.use(Select)
Vue.use(Upload)

