<template>
  <div>
    <a-card title="用户导航/用户列表管理">
      <a-row :gutter="20">
        <a-col :span="6">
          <a-input-search
            v-model="queryParam.username"
            placeholder="请输入搜索内容"
            size="large"
            allowClear
            @search="getUserList"
          >
            <a-button slot="enterButton"> 搜索 </a-button>
          </a-input-search>
        </a-col>

        <a-col :span="4">
          <a-button type="primary" @click="addUservisible = true">新增</a-button>
        </a-col>
      </a-row>
      <a-table
        :pagination="paginationOption"
        :dataSource="userlist"
        :columns="columns"
        rowKey="username"
      >
        <span slot="role" slot-scope="role">{{
          role === 2 ? "管理员" : "订阅者"
        }}</span>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" style="margin-right: 15px" @click="editUser(data.ID)">编辑</a-button>
            <a-button type="danger" style="margin-right: 15px" @click="deleteUser(data.ID)">删除</a-button>

          </div>
        </template>
      </a-table>
      <!-- 在table后面 有具体属于它的对象 -->
      <!-- table虾米那的pagination分页器 是一个对象 datasource数据源 是一个数组 columns列 是每一个列 然后在下面的data里面写对象具体的属性加值 -->
    </a-card>
    <!-- 新增用户区域 -->
    <a-modal
      closable
      title="新增用户"
      :visible="addUservisible"
      @ok="addUserOk"
      @cancel="addUserCancel"
      destroyOnClose
    >
      <a-form-model
        :model="userInfo"
        :rules="userInfoRules"
        ref="addUserInfoRef"
      >
        <a-form-model-item label="用户名" prop="username">
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="密码" prop="password">
          <a-input-password v-model="userInfo.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="确认密码" prop="checkpassword">
          <a-input-password v-model="userInfo.checkpassword"></a-input-password>
        </a-form-model-item>
        <a-form-model-item label="是否为管理员" prop="role">
          <a-select
            default-value="2"
            style="width: 120px"
            @change="addAdminChange"
          >
            <a-select-option key="1" value="1">是</a-select-option>
            <a-select-option key="2" value="2">否</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!-- 编辑用户区域 -->
    <a-modal
      closable
      title="编辑用户"
      :visible="editUserVisible"
      @ok="editUserOk"
      @cancel="editUserCancel"
    >
      <a-form-model
        :model="userInfo"
        :rules="userInfoRules"
        ref="addUserInfoRef"
      >
        <a-form-model-item label="用户名" prop="username">
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item label="是否为管理员" prop="role">
          <a-select
            default-value="2"
            style="width: 120px"
            @change="addAdminChange"
          >
            <a-select-option key="1" value="1">是</a-select-option>
            <a-select-option key="2" value="2">否</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
// columns 这里定义了每一列的展示内容
const columns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    width: '10%',
    align: 'center'
    // key: 'ID'
  },
  {
    title: '用户名',
    dataIndex: 'username',
    width: '20%',
    align: 'center'

    // key: 'username'
  },
  {
    title: '角色',
    dataIndex: 'role',
    width: '20%',
    key: 'role',
    align: 'center',
    scopedSlots: { customRender: 'role' }
  },
  {
    title: '操作',
    width: '30%',
    align: 'center',
    scopedSlots: { customRender: 'action' }

    // key: 'action'
  }
]
export default {
  // data 封装本组件属性  这里写html各个对象的属性
  data () {
    return {
      // 1.分页器的配置
      paginationOption: {
        defaultCurrent: 1, // 当前页
        defaultPageSize: 2, // 每页页数
        pageSizeOptions: ['2', '5', '10'], // 每页页数的选择
        total: 0, // 一共多少条
        showSizeChanger: true,
        showTotal: (total) => `共${total}条`,
        // 这里的回调是上面选择 当前页和每页页数之后 触发调用下面获取每页页数函数
        onChage: (page, pageSize) => {
          // 将选择过后的页码赋值给当前页码 配置分页器
          this.paginationOption.defaultPagesize = pageSize
          this.paginationOption.defaultCurrent = page
        },
        // 每页条数 当前页
        onshowSizeChange: (current, size) => {
          this.paginationOption.defaultPageSize = size
          this.paginationOption.defaultCurrent = current
        }
      },
      // 我们在上面对前端表单对象创建了数组  再在下面方法进行了赋值
      userlist: [],
      columns,
      queryParam: {
        username: ''
      },
      addUservisible: false,
      editUserVisible: false,
      userInfo: {
        id: 0,
        username: '',
        password: '',
        checkpassword: '',
        role: 2
      },
      userInfoRules: {
        username: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.username === '') {
                callback(new Error('请输入用户名'))
              }
              if (
                [...this.userInfo.username].length < 4 ||
                [...this.userInfo.username].length > 12
              ) {
                callback(new Error('用户名应当在4到12个字符之间'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.password === '') {
                callback(new Error('请输入密码'))
              }
              if (
                [...this.userInfo.password].length < 6 ||
                [...this.userInfo.password].length > 20
              ) {
                callback(new Error('密码应当在6到20位之间'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],
        checkpassword: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.checkpassword === '') {
                callback(new Error('请输入密码'))
              }
              if (this.userInfo.password !== this.userInfo.checkpassword) {
                callback(new Error('密码不一致，请重新输入'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  },
  //  上面设置前端对象的属性
  created () {
    this.getUserList()
  },
  methods: {
    // 获取用户列表
    async getUserList () {
      // 2.请求用户列表路由 传入参数每页条数 当前页数
      // 这里只获取到后端传过来的data数据
      const { data: res } = await this.$http.get(
        'http://121.196.163.8:8081/api/v1/users',
        {
          params: {
            username: this.queryParam.username,
            pagesize: this.paginationOption.defaultPagesize,
            pagenum: this.paginationOption.defaultCurrent
          }
        }
      )
      console.log(res)
      if (res.status !== 200) return this.$message.error(res.message)
      // 3.这里赋值给分页器的数据源datasource : userlist
      this.userlist = res.data
      this.paginationOption.total = res.total
    },
    // 根据id删除用户
    async deleteUser (id) {
      this.$confirm({
        title: '确定要删除该用户吗?',
        content: '删除过后，无法恢复',
        onOk: async () => {
          const res = await this.$http.delete(
            `http://121.196.163.8:8081/api/v1/user/${id}`
          )
          console.log(res)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('删除成功')
          this.getUserList()
        },
        onCancel: () => {
          this.$message.info('已取消')
        }
      })
    },
    // 添加用户
    addUserOk () {
      // 1.通过引用对象进行验证
      this.$refs.addUserInfoRef.validate(async (valid) => {
        // 2.如果未通过验证 就返回提示信息
        if (!valid) return this.$message.error('参数不符合要求，请重新输入Î')
        // 3.如果通过验证 通过axios发送post请求提交信息
        const { data: res } = await this.$http.post(
          'http://121.196.163.8:8081/api/v1/user/add',
          {
            username: this.userInfo.username,
            password: this.userInfo.password,
            role: this.userInfo.role
          }
        )
        // 4.根据返回的状态码做出判断
        if (res.status !== 200) return this.$message.error(res.message)
        this.$message.success('添加用户成功')

        // 5.添加用户后关闭窗口
        this.addUservisible = false
        // 6.获取添加用户后新的用户数组
        this.getUserList()
      })
    },
    // 取消添加用户
    addUserCancel () {
      this.$refs.addUserInfoRef.resetFields()
      this.addUservisible = false
    },
    // 设置是否为管理员
    addAdminChange (value) {
      console.log(typeof (value))
      this.userInfo.role = Number(value)
    },
    // =================================================================
    // 编辑用户
    async editUser (id) {
      this.editUserVisible = true
      const { data: res } = await this.$http.get(`http://121.196.163.8:8081/api/v1/user/${id}`)
      console.log(res.data)
      this.userInfo = res.data
      this.userInfo.id = id
    },
    // 关闭编辑用户框
    editUserCancel () {
      this.$refs.addUserInfoRef.resetFields()
      this.editUserVisible = false
      this.$message.info('编辑已取消')
    },
    resetUser (id) {

    },
    // 编辑用户ok之后
    editUserOk () {
      // 1.通过引用对象进行验证
      this.$refs.addUserInfoRef.validate(async (valid) => {
        // 2.如果未通过验证 就返回提示信息
        if (!valid) return this.$message.error('参数不符合要求，请重新输入Î')
        // 3.如果通过验证 通过axios发送post请求提交信息
        const { data: res } = await this.$http.put(
          `http://121.196.163.8:8081/api/v1/user/${this.userInfo.id}`,
          {
            username: this.userInfo.username,
            role: this.userInfo.role
          }
        )
        // 4.根据返回的状态码做出判断
        if (res.status !== 200) return this.$message.error(res.message)
        this.$message.success('更新用户成功')

        // 5.添加用户后关闭窗口
        this.editUserVisible = false
        // 6.获取添加用户后新的用户数组
        this.getUserList()
      })
    }
  }
}
</script>
